.MenuBarContainer{
    position: fixed;
    left: -300px; /* Start off-screen */
    top: 0;
    width: 300px;
    height: 100%;
    background-color: #040b14;
    display: flex;
    flex-direction: column;
    z-index: 100;
    transition: left 0.3s ease;
}

.open{
    left:0
}

.Profile{
    display: block;
    height: 240;
    width: 100%;
    border-bottom: 2px solid #2c2f3f;
}

.ProfilePic{
    border: 8px solid #2c2f3f;
    border-radius: 50%;
    aspect-ratio: 1;
    width: 120px;
    margin-top: 15px;
}

.TextName{
    font-weight: 600;
    color: white;
    font-size: 24px;
    margin: 0;
    text-align: center;
    font-family: "Poppins", sans-serif;;
}
a{
    text-decoration: none;
    color: white
}

.SocialMediaContainer{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.SocialTag{
    aspect-ratio: 1;
    height: 36px;
    border-radius: 50%;
    background-color: #2c2f3f;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
}

.SocialTag:hover{
    background-color: #149ddd;
}

.Nav-Menu{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.NavbarItem{
    max-width: 95%;
    height: fit-content;
    margin: 5px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 15px;
    color: grey;
    display: flex;
    flex-direction: row;
    padding-left: 15px;
    /* Added transition property - helps smooth out on hover */
    transition: background-color 0.3s ease, padding 0.3s ease, color 0.3s ease;
}

.NavbarItem:hover{
    background-color: green;
    color: white;
    padding-top:30px;
    padding-bottom: 30px;
    border: 2px solid white ;
}